<template>
    <div class="box">
      <!--房源列表页-->
      <div v-if="!isShowSearch">
        <head-top goBack="true"/>
        <div class="content">
          <!--搜索-->
          <div class="my-search clear">
            <div class="my-searchbar-inner left clear">
              <div class="my-search-dropdown left" >
                  <div class="" @click="openHouseTypePop">
                    {{houseType == 1?"二手房":'租房' }}<i class="icon iconfont">&#xe62d;</i>
                  </div>
                  <ul v-if="isShowHouseType" class="house-type-ul">
                    <li> <i class="icon iconfont arrow-top">&#xe65d;</i></li>
                    <li :class="{'house-type-active':houseType==1}"><span @click='selectHouseType(1)'>二手房</span></li>
                    <li :class="{'house-type-active':houseType==2}"><span @click='selectHouseType(2)'>租房</span></li>
                  </ul>
              </div>
              <div class="my-search-input left">
                <input type="search" placeholder="请输入商圈或小区名" @focus="isShowSearch=true" v-model="communityName">
              </div>
            </div>
            <div class="my-search-right left" @click="toggleMore">
              <i class="icon iconfont iconfont-more">&#xe641;</i>
            </div>
            <ul class="more-ul" v-if="morePopVisible">
              <li><i class="icon iconfont arrow">&#xe65d;</i></li>
              <li><router-link to="/myCare"><span><i class="icon iconfont my-care">&#xe609;</i></span>我的关注</router-link></li>
              <li><router-link to="/lookHouseIndex"><span><i class="icon iconfont look-house">&#xe610;</i></span>看房预约</router-link></li>
              <li><router-link to="/signSearch"><span><i class="icon iconfont sign-search">&#xe60b;</i></span>签约查询</router-link></li>
            </ul>
          </div>
          <!--过滤导航-->
          <div class="condition-filter clear" @touchmove.prevent>
            <ul class="left clear">
              <li @click = 'opentFilter("district")' :class="{'select-active':filterTypeActive == 'district'}">
                {{filterShowText.district.name}}
                <i v-if="filterType != 'district'" class="icon iconfont">&#xe65e;</i>
                <i  v-if="filterType == 'district'" class="icon iconfont select-active">&#xe65d;</i>
              </li>
              <li @click = 'opentFilter("price")' :class="{'select-active':filterTypeActive == 'price'}">
                {{filterShowText.price.name}}
                <i v-if="filterType != 'price'" class="icon iconfont">&#xe65e;</i>
                <i  v-if="filterType == 'price'" class="icon iconfont select-active">&#xe65d;</i>
              </li>
              <li @click = 'opentFilter("roomType")' :class="{'select-active':filterTypeActive == 'roomType'}">
                {{filterShowText.roomType.name}}
                <i v-if="filterType != 'roomType'" class="icon iconfont">&#xe65e;</i>
                <i  v-if="filterType == 'roomType'" class="icon iconfont select-active">&#xe65d;</i>
              </li>
              <li @click = 'opentFilter("filterMore")' :class="{'select-active':filterTypeActive == 'filterMore'}">
                {{filterShowText.filterMore.name}}
                <i v-if="filterType != 'filterMore'" class="icon iconfont">&#xe65e;</i>
                <i v-if="filterType == 'filterMore'" class="icon iconfont select-active">&#xe65d;</i>
              </li>
              <li @click = 'opentFilter("filterOrder")' :class="{'select-active':filterTypeActive == 'filterOrder'}">
                <i class="icon iconfont">&#xe656;</i></li>
            </ul>
          </div>
          <!--搜索结果为空-->
          <div v-if="recomment > 0" class="search-empty">
            <div class="search-empty-tip">找不到您搜索的房源</div>
            <div class="recomment-tip">为您推荐</div>
          </div>
          <!--房源结果列表-->
          <div v-if="houseLists.length>0">
            <ul class="house-list"
                v-infinite-scroll="loadMore"
                infinite-scroll-disabled="loading"
                infinite-scroll-distance="0">
              <li class="house-item clear" v-for="item in houseLists" :key="item.id">
                <router-link
                  :to="{name:houseTypeDetail[houseType], params:{cityId:'hz', houseId:item.id, userType:'customer', houseType:houseType}}">
                  <house-item :item="item" :houseType="houseType"/>
                </router-link>
              </li>
            </ul>
            <p v-if="!isLoadAll" class="loading-more"><span class="loading-more-span"><i class="icon iconfont loading-more-icon">&#xe6ae;</i></span><span class="loading-more-text">加载更多</span></p>
            <p v-if="isLoadAll" class="loading-more">没有更多数据了！</p>
          </div>

          <!--价格-->
          <div v-if="filterType == 'price'" class="filter-price"  @touchmove.prevent>
            <div class="filter-title">价格区间（{{houseType == 1?'万':'元'}}）</div>
            <div class="price-between-input">
              <input
                class="left" v-model="houseParams[houseType].priceMin"
                @focus="clearNowFilter()"
                maxlength="4"
                @input="houseParams[houseType].priceMin.length>4?houseParams[houseType].priceMin = houseParams[houseType].priceMin.slice(0, 4):''"
                type="number"/>
              <span><i class="icon iconfont iconfont-heng">&#xe6f1;</i></span>
              <input
                class="right"
                v-model="houseParams[houseType].priceMax"
                @focus="clearNowFilter"
                @input="houseParams[houseType].priceMax.length>4?houseParams[houseType].priceMax = houseParams[houseType].priceMax.slice(0, 4):''"
                maxlength="4" type="number"/>
            </div>
            <ul class="filter-select">
              <li
                v-for="item in filterList[houseType].price.child" :key="item.id"
                class=""
                :class="{'filter-select-active': houseParams[houseType].filterIds.indexOf(item.id) >= 0}"
                @click="setFilterValue($event,item)">{{item.child_name}}
              </li>
            </ul>
            <div class="filter-btn">
              <button class="unlimit-btn" @click="unlimit">不限</button>
              <button class="confirm-btn" @click="filterConfirm">确定</button>
            </div>
            <!--遮罩-->
            <div class="filter-mask" @click = 'opentFilter("price")'></div>
          </div>
          <!--区域-->
          <div v-if="filterType == 'district'" class="filter-district" @touchmove.prevent>
            <div class="clear">
              <!--区域-->
              <ul class="district-ul left" @touchmove.stop>
                <li v-for="(districtItem,index) in district"
                    :class="{'select-font-active': activDistrictIndex == index}"
                    @click="setCheckDistrictValue($event,index)">
                  {{districtItem.district}}
                </li>
              </ul>
              <!--板块-->
              <ul class="street-ul left clear" @touchmove.stop>
                <li v-for="streetItem in district[activDistrictIndex].street"
                    @click="setCheckStreetValue($event,streetItem)"
                    :class="{'street-active': houseParams[houseType].areaIds == streetItem.id}">
                  {{streetItem.name}}
                </li>
              </ul>
            </div>
            <!--遮罩-->
            <div class="filter-mask" @click = 'opentFilter("district")' @touchmove.prevent></div>
          </div>
          <!--房型-->
          <div v-if="filterType == 'roomType'" class="filter-room-type"  @touchmove.prevent>
            <ul class="filter-select">
              <li v-for="item in filterList[houseType].room.child" :key="item.id"
                  :class="{'filter-select-active': houseParams[houseType].filterIds.indexOf(item.id) >= 0}"
                  @click="setFilterValue($event,item)">
                {{item.child_name}}
              </li>
            </ul>
            <div class="filter-btn">
              <button class="unlimit-btn" @click="unlimit">不限</button>
              <button class="confirm-btn" @click="filterConfirm">确定</button>
            </div>
            <!--遮罩-->
            <div class="filter-mask" @click = 'opentFilter("price")'></div>
          </div>
          <!--筛选-->
          <div v-if="filterType == 'filterMore'" class="filter-more"  @touchmove.prevent>
            <div class="filter-title">建筑面积（㎡）</div>
            <ul class="filter-select">
              <li
                v-for="item in filterList[houseType].buildarea.child" :key="item.id"
                :class="{'filter-select-active': houseParams[houseType].filterIds.indexOf(item.id) >= 0}"
                @click="setFilterValue($event,item)">{{item.child_name}}
              </li>
            </ul>
            <div class="filter-title">楼龄</div>
            <ul class="filter-select">
              <li
                v-for="item in filterList[houseType].buildyear.child" :key="item.id"
                :class="{'filter-select-active': houseParams[houseType].filterIds.indexOf(item.id) >= 0}"
                @click="setFilterValue($event,item)">{{item.child_name}}
              </li>
            </ul>

            <div class="filter-title">朝向</div>
            <ul class="filter-select">
              <li
                v-for="item in filterList[houseType].forward.child" :key="item.id"
                :class="{'filter-select-active': houseParams[houseType].filterIds.indexOf(item.id) >= 0}"
                @click="setFilterValue($event,item)">{{item.child_name}}
              </li>
            </ul>
            <div class="filter-btn">
              <button class="unlimit-btn" @click="clearNowFilter">重置</button>
              <button class="confirm-btn" @click="filterConfirm">确定</button>
            </div>
            <!--遮罩-->
            <div class="filter-mask" @click = 'opentFilter("price")'></div>
          </div>
          <!--排序-->
          <div v-if="filterType == 'filterOrder'" class="filter-order">
            <div class="clear">
              <ul class="filter-order-ul left">
                <li v-for="(item,index) in orderFilter[houseType]"
                    :class="{'select-font-active': houseParams[houseType].orderColumn == item.orderColumn&&houseParams[houseType].orderBy == item.orderBy}"
                    @click="setOrderValue($event,item)">
                  {{item.describe}}
                </li>
              </ul>
            </div>
            <!--遮罩-->
            <div class="filter-mask" @click = 'opentFilter("district")' @touchmove.prevent></div>
          </div>
        </div>
      </div>
      <!--搜索页-->
      <search
        v-if="isShowSearch"
        :houseType="houseType"
        @hideSearch="isShowSearch = false"
        @searchHouse="searchHouse">
      </search>
      <!--全屏遮罩-->
      <div class="full-mask" v-if="morePopVisible" @touchmove.prevent @click="morePopVisible = false"></div>
    </div>
</template>
<script>
    import api from '../../api/axios'
    import headTop from '../../components/header/head'
    import houseItem from '../../components/common/houseItem'
    import search from '../../page/search/search'
//    import $alert from '../../components/common/alert/alert.js'

    export default {
        data(){
            return {
              houseParams:{
                1:{
                  cityId:"hz",
                  communityId:"",
                  areaIds:"",
                  priceMin:"",//最小价格
                  priceMax:"",//最大价格
                  filterIds:[],
                  pageSize:10,
                  pageIndex:1,
                  orderBy:"",
                  orderColumn:'',
                },//二手房房源请求参数
                2:{
                  cityId:"hz",
                  communityId:"",
                  areaIds:"",
                  priceMin:"",//最小价格
                  priceMax:"",//最大价格
                  filterIds:[],
                  pageSize:10,
                  pageIndex:1,
                  orderBy:"",
                  orderColumn:'',
                },//租房房源请求参数
              },
              houseParamsInit:{
                1:{
                  cityId:"hz",
                  communityId:"",
                  areaIds:"",
                  priceMin:"",//最小价格
                  priceMax:"",//最大价格
                  filterIds:[],
                  pageSize:10,
                  pageIndex:1,
                  orderBy:"",
                  orderColumn:'',
                },//二手房房源请求参数
                2:{
                  cityId:"hz",
                  communityId:"",
                  areaIds:"",
                  priceMin:"",//最小价格
                  priceMax:"",//最大价格
                  filterIds:[],
                  pageSize:10,
                  pageIndex:1,
                  orderBy:"",
                  orderColumn:'',
                },//租房房源请求参数
              },
              communityName:'',
              houseLists:[],//房源列表
              recomment:0,//是否为推荐房源
              houseType:this.$store.state.activeInfo.houseType?this.$store.state.activeInfo.houseType:1,//房源类型
              filterType:'',//过滤选项卡
              filterTypeActive:'',//选中的选项卡
              filterList:{
                  1:{},
                  2:{}
              },//获取过滤列表
              district:[],//区域板块列表
              streetInDistrictIds:[],//板块不限列表（板块不限）
              activDistrictIndex:0,//选中的小区
              orderFilter:{
                  1:[
                    {orderColumn:"id",orderBy:"desc",describe:"最新发布"},
                    {orderColumn:"price",orderBy:"asc",describe:"总价从低到高"},
                    {orderColumn:"price",orderBy:"desc",describe:"总价从高到底"},
                    {orderColumn:"avgprice",orderBy:"asc",describe:"单价从低到高"},
                    {orderColumn:"buildarea",orderBy:"desc",describe:"面积从大到小"},
                  ],
                2:[
                  {orderColumn:"id",orderBy:"desc",describe:"最新发布"},
                  {orderColumn:"price",orderBy:"asc",describe:"租金从低到高"},
                  {orderColumn:"price",orderBy:"desc",describe:"租金从高到底"},
                ],
              },
              morePopVisible:false,//更多菜单弹出选项
              isShowSearch:false,//展示搜索页
              isShowHouseType:false,//展示房源类型弹框
              houseTypeDetail:{
                1:'houseBuyDetail',
                2:'houseRentDetail',
                3:'villageDetail',
              },//详情类型
              isLoadAll: false, //是否加载完所有数据
              loading:false,
              filterShowText:{
                district:{name:'区域',select:[]},
                price:{name:'价格',select:[]},
                roomType:{name:'房型',select:[]},
                filterMore:{name:'筛选',select:[]},
              }
            }
        },
        components: {
          headTop,
          search,
          houseItem,
        },
        created(){
          this.gethouseLists();
          this.getFilterList();
          this.getDistrict();
        },
        computed: {

        },
        methods: {
          gethouseLists(isLoadMore){
            if(!isLoadMore){ //不是加载更多时
              this.houseParams[this.houseType].pageIndex = 1
            }
            if (this.houseType == 1){

              //获取出售房源列表
              let params = this.houseParams[this.houseType];
              //openId:this.$route.query ? this.$route.query.openId:"",
              params.openId = this.$route.query? this.$route.query.openId : "";
              params.code = this.$route.query.code ? this.$route.query.code : "";
              console.log(params)
              api.getSellHouseList(params)
                .then( res => {
                  if (res.data.success){
                    this.recomment = res.data.result.recomment;//是否为推荐房源
                        if(isLoadMore){
                          this.houseLists = this.houseLists.concat(res.data.result.list);
                          if (res.data.result.list.length < this.houseParams[this.houseType].pageSize){//是否已经加载完所有数据
                            this.loading = true;
                            this.isLoadAll = true;
                          }else{
                            this.loading = false
                            this.isLoadAll = false;
                          }
                          console.log(this.houseLists)
                        }else{
                          this.houseLists = res.data.result.list;
                          if (res.data.result.list.length<this.houseParams[this.houseType].pageSize){ //是否已经加载完所有数据
                            this.loading = true
                            this.isLoadAll = true;
                          }else{
                            this.loading = false
                            this.isLoadAll = false;
                          }
                        }
                  }else{
                    this.$toast({
                      message: res.data.errorMessage,
                      position: 'bottom',
                      duration: 3000
                    });
                  }
                })
                .catch(res =>{
                  this.$toast({
                  message: res.data.errorMessage,
                  position: 'bottom',
                  duration: 3000
                });
                });
            }
            else if(this.houseType == 2){
              //获取出售房源列表
              let params = this.houseParams[this.houseType];
              console.log(params)
              api.getRentHouseList(params)
                .then( res => {
                    console.log(res)
                  if (res.data.success){
                    if(isLoadMore){
                      this.houseLists = this.houseLists.concat(res.data.result.list);
                      if (res.data.result.list.length < this.houseParams[this.houseType].pageSize){//是否已经加载完所有数据
                        this.loading = true;
                        this.isLoadAll = true;
                      }else{
                        this.loading = false
                        this.isLoadAll = false;
                      }
                    }else{
                      this.houseLists = res.data.result.list;
                      if (res.data.result.list.length<this.houseParams[this.houseType].pageSize){ //是否已经加载完所有数据
                        this.loading = true
                        this.isLoadAll = true;
                      }else{
                        this.loading = false
                        this.isLoadAll = false;
                      }
                    }
                  }else{
                    this.$toast({
                      message: res.data.errorMessage,
                      position: 'bottom',
                      duration: 3000
                    });
                  }
                })
                .catch(res =>{
                  this.$toast({
                    message: res.data.errorMessage,
                    position: 'bottom',
                    duration: 3000
                  });
                });
            }
          },
          //获取过滤条件
          getFilterList(){
//              console.log(this.filterList)
//            if (this.filterList && this.filterList.price){
//                return
//            }

            api.getFilterList({type:1})
              .then( res => {
                if (res.data.success){
                  this.filterList[1] = res.data.result

                }else{
                  this.$toast({
                  message: res.data.errorMessage,
                  position: 'bottom',
                  duration: 3000
                });
                }
              })
              .catch(res =>{
                this.$toast({
                  message: res.data.errorMessage,
                  position: 'bottom',
                  duration: 3000
                });
              });
            api.getFilterList({type:2})
              .then( res => {
                if (res.data.success){
                  this.filterList[2] = res.data.result
                }else{
                  this.$toast({
                    message: res.data.errorMessage,
                    position: 'bottom',
                    duration: 3000
                  });
                }
              })
              .catch(res =>{
                this.$toast({
                  message: res.data.errorMessage,
                  position: 'bottom',
                  duration: 3000
                });
              });
          },
          //获取区域
          getDistrict(){
            let params = {
              city:"hz"
            };
            api.getDistrict(params)
              .then( res => {
                if (res.data.success){
                    let district =  res.data.result;
                    let streetInDistrictIds = {}
                  district.unshift({district:'不限',street:[]});
                  district.forEach((item,index)=>{
                    streetInDistrictIds[index] = [];
                    item.street.forEach(streetItem=>{
//                        console.log(index)
//                      console.log(streetItem.id)
                      streetInDistrictIds[index].push(streetItem.id)
                    })
                    item.street.unshift({id:0,name:'不限'})
                  });

                  this.district = district;
                  this.streetInDistrictIds = streetInDistrictIds;
                  console.log(this.district)
                }else{
                  this.$toast({
                  message: res.data.errorMessage,
                  position: 'bottom',
                  duration: 3000
                });
                }
              })
              .catch(res =>{
                  console.log(res)
                this.$toast({
                  message: res,
                  position: 'bottom',
                  duration: 3000
                });
              });
          },
          //获取选中房源类型
          selectHouseType(houseType){
            this.houseType=houseType;
            this.isShowHouseType = false;//关闭弹框

//            this.getFilterList();
            //清空筛选条件
            this.clearAllFilter();
            this.gethouseLists();
          },
          //打开筛选弹框
          opentFilter(filterType){
              if (this.isShowHouseType){ //房源类型弹框打开时
                  return
              }
              if (this.filterType){
                this.filterType = ''
                this.filterTypeActive = ''
              }else{
                this.filterType = filterType
                this.filterTypeActive = filterType
              }
            console.log(this.filterList)
          },
          //清空筛选条件
          clearAllFilter(){
            let  filterShowText = {
                  district:{name:'区域',select:[]},
                  price:{name:'价格',select:[]},
                  roomType:{name:'房型',select:[]},
                  filterMore:{name:'筛选',select:[]},
                }
            this.houseParams[this.houseType] = this.houseParamsInit[this.houseType];
            this.filterShowText = filterShowText;
            this.filterTypeActive = '';
            this.activDistrictIndex = 0;
          },
          //清空传入的筛选条件
          clearFilterByChild(child){
            child.forEach((item)=>{
              let index = this.houseParams[this.houseType].filterIds.indexOf(item.id)
              if(index>=0){
                this.houseParams[this.houseType].filterIds.splice(index,1)
              }
            })
          },
          //清空价格筛选条件
          clearNowFilter(){
              if(this.filterType == 'price'){//价格
                let child = this.filterList[this.houseType].price.child;
                this.clearFilterByChild(child);
                this.filterShowText. price = {name:'价格',select:[]};
              }else if(this.filterType == 'roomType'){//房型
                let child = this.filterList[this.houseType].room.child;
                this.clearFilterByChild(child);
              }else if(this.filterType == 'filterMore'){//筛选
                let buildareaChild = this.filterList[this.houseType].buildarea.child;
                let buildyearChild = this.filterList[this.houseType].buildyear.child;
                let forwardChild = this.filterList[this.houseType].forward.child;
                let child = buildareaChild.concat(buildyearChild,forwardChild);console.log(forwardChild)
                this.clearFilterByChild(child);
              }
          },
          //打开房源类型弹框
          openHouseTypePop(){
            this.filterType = '';
            this.isShowHouseType = !this.isShowHouseType;
          },
          //设置价格过滤条件
          setFilterValue(event,item){
            let idIndex = this.houseParams[this.houseType].filterIds.indexOf(item.id);
            let selectIndex = this.filterShowText[this.filterType].select.indexOf(item.child_name);//显示选中的条件
            if (idIndex == "-1") {
              this.houseParams[this.houseType].filterIds.push(item.id);
              this.filterShowText[this.filterType].select.push(item.child_name);//显示选中的条件
            } else {
              this.houseParams[this.houseType].filterIds.splice(idIndex,1);
              this.filterShowText[this.filterType].select.splice(selectIndex,1);
            }
            if(this.filterType == 'price'){
              this.houseParams[this.houseType].priceMin = '';
              this.houseParams[this.houseType].priceMax = '';
            }
          },
          //过滤确认按钮
          filterConfirm(){
            this.gethouseLists();
            //获取输入的价格
            let priceMin = this.houseParams[this.houseType].priceMin;
            let priceMax = this.houseParams[this.houseType].priceMax;
            let priceUnit = this.houseType == 1?'万元':'元';
            //设置选中条件名
            if(this.filterShowText[this.filterType].select.length>1){
              this.filterShowText[this.filterType].name = '多选';
            }else  if(this.filterShowText[this.filterType].select.length==1){
              if(this.filterType == 'price'&&(priceMin || priceMax)){
                this.filterShowText[this.filterType].name = '多选';
              }else{
                this.filterShowText[this.filterType].name = this.filterShowText[this.filterType].select[0];
              }
            }else if(this.filterType == 'price' && this.filterShowText[this.filterType].select.length==0){//价格输入的情况
              if(priceMin && priceMax){
                this.filterShowText[this.filterType].name = priceMin+'-'+priceMax
              }else if(priceMin){
                this.filterShowText[this.filterType].name = priceMin+priceUnit+'以上'
              }else if(priceMax){
                this.filterShowText[this.filterType].name = priceMax+priceUnit+'以下'
              }
            }
            this.filterType = '';
          },
          //过滤不限按钮
          unlimit(){
//            this.houseParams[this.houseType] = this.houseParamsInit[this.houseType]//房源请求参数
            this.clearNowFilter();
            this.gethouseLists();
            this.filterType = '';
          },
          //设置区域滤条件
          setCheckDistrictValue(event,id){
            this.activDistrictIndex = id;
              if(id == 0){
                this.houseParams[this.houseType].areaIds = '';
                this.filterShowText.district.name = '区域';
                this.gethouseLists();
                this.filterType = ''
              }
          },
          //设置板块过滤条件
          setCheckStreetValue(event,streetItem){
              if(streetItem.id == 0){
                this.houseParams[this.houseType].areaIds = this.streetInDistrictIds[this.activDistrictIndex].join(',')
                this.filterShowText.district.name = this.district[this.activDistrictIndex].district;
              }else{
                this.houseParams[this.houseType].areaIds = streetItem.id;
                this.filterShowText.district.name = streetItem.name;
              }
            this.gethouseLists();
            this.filterType = ''

          },
          //设置排序条件
          setOrderValue(event,orderItem){
            this.houseParams[this.houseType].orderBy = orderItem.orderBy;
            this.houseParams[this.houseType].orderColumn = orderItem.orderColumn;
            this.gethouseLists();
            this.filterType = ''
          },
          //更多菜单
          toggleMore(){
              if(this.filterType){
                return
              }
              this.morePopVisible = !this.morePopVisible
          },
          //展示搜索页
          showSearch(){
              this.isShowSearch = true
          },
          //通过小区搜索房源(搜索子组件触发)
          searchHouse(community){
              console.log(community)
            this.communityName = community.communityName
            this.houseParams[this.houseType] = this.houseParamsInit[this.houseType] //房源请求参数
            this.houseParams[this.houseType].communityId = community.communityId;
            this.gethouseLists();
            this.isShowSearch = false //不展示搜索组键

          },
          //加载更多
          loadBottom(){
            this.houseParams[this.houseType].pageIndex++;
            console.log(this.houseParams[this.houseType].pageIndex)
            this.gethouseLists(true)

            this.$refs.loadmore.onBottomLoaded();
          },
//          loadMore() {
//            this.loading = true;
//            setTimeout(() => {
//              let last = this.list[this.list.length - 1];
//              for (let i = 1; i <= 10; i++) {
//                this.list.push(last + i);
//              }
//              this.loading = false;
//            }, 2500);
//            console.log(123)
//          },
          loadMore() {
            this.loading = true;
            this.houseParams[this.houseType].pageIndex++;
            console.log(this.houseParams[this.houseType].pageIndex)
            this.gethouseLists(true)
//              this.loading = false;
            console.log(123)
          }
        },
        watch:{
//          houseParams(){
//              this.gethouseLists();
//          },
//          houseType(){
//
//          }
        }
    }

</script>

<style lang="scss" scoped>

  .box{
    font-size: 1.6rem;
  }
  /*公用样式封装*/
  /*边框*/
  @mixin border {
    border: 0.05rem solid #f5f5f5;
  }
  @mixin border-top {
    border-top: 0.05rem solid #f5f5f5;
  }
  /*过滤弹框*/
  @mixin filter-wrap{
    width: 100%;
    font-color:#424242;
    font-size: 1.5rem;
    position: absolute;
    z-index: 6;
    top: 16.5rem;
    background-color: #fff;
  }
  /*过滤标题*/
  @mixin filter-title{
    margin:0 2rem;
//    @include border-top;
    /*padding: 1rem 0 2rem 0;*/
    padding-bottom:1rem ;
    font-size: 1.5rem;
  }
  /*过滤选择项*/
  @mixin filter-select{
    overflow: hidden;
    clear: both;
    margin: 0 2rem;
    li {
      float: left;
      width: 30%;
      height: 2.5rem;
      line-height: 2.5rem;
      text-align: center;
      margin-bottom: 1rem;
      margin-right: 3%;
      border-radius: 0.025rem;
      /*border:1px solid #f5f5f5;*/
      @include border;
      box-sizing: border-box;
      font-size: 1.3rem;

    }
  }
  /*选项选中状态*/
  @mixin filter-select-active{
    background-color: #fbf6ee;
    color:#ffc16b;
  }
  /*过滤按钮*/
  @mixin filter-btn{
    overflow: hidden;
    width: 100%;
    /*margin-top: 2rem;*/
    button{
      width: 50%;
      float: left;
      height: 4rem;
    }
    button:nth-child(1){
      background-color:#f8f8f8 ;
    }
    button:nth-child(2){
      font-size: 1.6rem;
      color: #754501;
      background-color: #ffc16b;
    }
  }
    /**搜索样式*/
    .my-search{
      margin-top: 1.2rem;
      height: 3.5rem;
      font-size: 1.3rem;
      padding:0 2rem;
      div{
        height: 3.5rem;
        line-height: 3.5rem;
      }
      .my-searchbar-inner{
        background-color: #f5f5f5;
        border-radius: 6px;
        padding-left: 1rem;
        width:85%;
        // width: 29rem;
      }
      /*下拉样式*/
      .my-search-dropdown{
        position: relative;
        width: 30%;
        font-size: 1.3rem;
        .house-type-ul{
          width: 9rem;
          top: 5rem;
          position: absolute;
          z-index: 6;
          /*left: 2rem;*/
          background-color: #fff;
          font-size: 1.3rem;
          border-radius: 2px;
          box-shadow: 0px 0px 10px -3px rgba(0,0,0,0.5);
          li:not(:first-child){
            height: 3.5rem;
            line-height: 3.5rem;
            padding:0 2rem;
            &:nth-child(3){
              @include border-top;
            }
            a{
              color: #424242;
            }
          }
          li:first-child{
            width: 13px;
            height: 10px;
            overflow: hidden;
            line-height: 10px;
            background-color: #fff;
            position: absolute;
            top: -9px;
            left: 1rem;
            .arrow-top{
              color:#fff;
              font-size: 1.6rem;
            }
          }
          .house-type-active{
            color: #ffc16b;
          }
        ;
        }
      }
      .my-search-input{
        /*border: 0;*/
        padding-left: 0.5rem;
        width: 70%;
        color: #424242;
        input{
          background-color: transparent;
          width: 100%;
        }
      }
      .my-search-right{
        width: 15%;
        text-align: right;
        .iconfont-more{font-size: 2.5rem};
      }
      .more-ul{
        position: absolute;
        z-index: 6;
        right: 2rem;
        top: 10rem;
        background-color: #fff;
        font-size: 1.3rem;
        border-radius: 6px;
        li:not(:first-child){
          height: 3.5rem;
          line-height: 3.5rem;
          padding: 0 1rem;
          &:nth-child(n+3){
            @include border-top;
          }
          a{
            color: #424242;
            display: inline-block;
            width: 100%;
          }
          span{
            display: inline-block;
            width: 2rem;
            /*margin: 0 .5rem 0 1rem;*/
          }
        }
        .arrow{
          color:#fff;
          position: absolute;
          top: -1.5rem;
          right: 0.5rem;
          font-size: 2.5rem;
        }
        .my-care{
          color:#ffc16b ;
          font-size: 1.9rem;
          vertical-align: middle;
        }
        .look-house{
          color:#5c5990 ;
          font-size: 2.2rem;
          vertical-align: bottom;
        }
        .sign-search{
          color:#eed7b5 ;
          font-size: 1.9rem;
          vertical-align: middle;
        }
      }
    }

    /**条件过滤*/
    .condition-filter{
      padding:3rem 2rem 0 2rem;
      ul{
        padding-bottom: 1.5rem;
        width: 100%;
        display: flex;
      }
      li{
        /*float: left;*/
        /*width: 16%;*/
        /*margin-left:5%;*/
        text-align: center;
        font-size: 1.5rem;
        flex:1 1 auto;
        li:last-child{
          width: 1rem;
        }
      }
      /*li:last-child{*/
        /*text-align: center;*/
      /*}*/
      .select-active{
        color:#ffc16a;
      }
    }

  /*价格筛选*/
    .filter-price{
      @include filter-wrap;
      .filter-title{
        @include filter-title;
      }
      .price-between-input {
        clear: both;
        margin:0 2rem 1rem 2rem;
        line-height: 2rem;
        height: 2.5rem;
        input{
          height: 100%;
          text-align: center;
          font-size: 1.6rem;
          box-sizing:border-box;
          /*border: 1px solid #f5f5f5;*/
          @include border;
          width: 45%;
        }
        span{
          display: inline-block;
          width: 10%;
          text-align: center;
          /*height: 100%;*/
        }
        .iconfont-heng{
          font-size: 2rem;
        }
      }
      .filter-select{
        @include filter-select
      }
      //选中状态
      .filter-select-active {
        @include filter-select-active;
      }
      /*过滤按钮*/
      .filter-btn{
        @include filter-btn;
      }
    }

  /*区域筛选*/
    .filter-district{
      @include filter-wrap;
      top: 15rem;
      ul{
        width: 50%;
        height: 29rem;
        overflow: scroll;
        li{
          padding-left: 2rem;
          height: 4rem;
          /*text-align: center;*/
          line-height: 4rem;
        }
      }
      .district-ul{
        background-color: #f8f8f8;
        //选中状态
        .select-font-active{
          color:#ffc16b;
        }
      }
      .street-ul{
        background-color: #f2f2f2;
        //选中状态
        .street-active{
          background-color:#f7ddba;
        }
      }
    }
    /*房型筛选*/
    .filter-room-type{
      @include filter-wrap;
      .filter-select{
        @include filter-select;
      }
      //选中状态
      .filter-select-active {
        @include filter-select-active;
      }
      /*过滤按钮*/
      .filter-btn{
        @include filter-btn;
      }
    }
    /*价格筛选*/
    .filter-more{
      @include filter-wrap;
      .filter-title{
        @include filter-title;
      }
      .filter-select{
        @include filter-select;
      }
      //选中状态
      .filter-select-active {
        @include filter-select-active;
      }
      /*过滤按钮*/
      .filter-btn{
        @include filter-btn;
      }
    }
    /*排序*/
    .filter-order{
      @include filter-wrap;
      ul{
        width: 100%;
        /*height: 29rem;*/
        overflow: scroll;
        li{
          padding-left: 2rem;
          height: 4rem;
          text-align: center;
          line-height: 4rem;
          /*border-top: 1px solid #f5f5f5;*/
          @include border-top;
        }
        //选中状态
        .select-font-active{
          color:#ffc16b;
        }
      }
    }
    /*遮罩*/
    .filter-mask{
      /*z-index: 5;*/
      width: 100%;
      height: 40rem;
      position: absolute;
      /*top: 0;*/
      /*bottom: 0;*/
      background-color: rgba(0,0,0,0.5);
    }
    /*全屏遮罩*/
    .full-mask{
    /*z-index: 5;*/
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0,0,0,0.5);
  }

    /*搜索结果为空*/
    .search-empty{
      .search-empty-tip{
        height: 7rem;
        line-height: 7rem;
        text-align: center;
        color: #414141;
        background-color: #f8f8f8;
      }
      .recomment-tip{
        font-weight: bold;
        padding:1rem 0 1rem 2rem;
        color:#444444;
      }
    }
  /**列表*/
  .house-list {
    padding: 0 2rem;
    li {
      @include border-top;
      /*background-color: #ccc;*/
    }
  }
/*加载中*/
  .loading-more{
    text-align:center;
    line-height: 2.6rem;
    @keyframes loading {
      0% {
        transform: rotate(0deg);
      }
      100% {
        transform: rotate(360deg);
      }
    }
    .loading-more-span{
      display: inline-block;
      animation:loading 1.2s infinite linear  both
    }
    .loading-more-text{
      padding-left: 1rem;
    }
    .loading-more-icon{
      font-size: 2.6rem;
    }
  }
</style>



